import React, { Fragment, useState, useRef, useEffect } from 'react';
import classNames from 'classnames';
import { Variable } from '../Utility/Utility';
import './index.scss';

export default function Text(props) {
  const {
    className,
    style,
    type,
    align, // 水平对齐: left, center, right
    verticalAlign, // 垂直对齐: top, middle, bottom
    info,
    theme, // normal, weak, success, error
    overflow, // 控制隐藏行数
    show, // 特殊显示更多
  } = props;
  const [actived, setActived] = useState(0);
  const cls = classNames(
    `${Variable}-text`,
    className,
    !!align ? [`${Variable}-text--${align}`] : '',
    !!verticalAlign ? [`${Variable}-text--${verticalAlign}`] : '',
    !!theme ? [`${Variable}-text--${theme}`] : '',
    !!overflow ? [`${Variable}-text--overflow${overflow}`] : ''
  );
  const moreCls = classNames(
    `${Variable}-text`,
    className,
    !!overflow ? [`${Variable}-text-box${overflow}`] : '',
    !!actived ? 'is-actived' : ''
  );
  const allRef = useRef(null);
  const prepareRef = useRef(null);
  const [allHeight, setAllHeight] = useState(0);
  const [prepareHeight, setPrepareHeight] = useState(0);
  const showClick = () => {
    setActived(!actived);
  };
  const dom = () => {
    const allRectHeight = allRef.current && allRef.current.clientHeight;
    const prepareHeight = prepareRef.current && prepareRef.current.clientHeight;
    setAllHeight(allRectHeight);
    setPrepareHeight(prepareHeight);
  };
  useEffect(() => {
    show === 'more' && type !== 'div' && dom();
  });
  // console.log(allHeight, prepareHeight);
  // console.log(info())
  return (
    <Fragment>
      {show !== 'more' ? (
        type === 'div' ? (
          <div className={cls} style={{ ...style }}>
            {info}
          </div>
        ) : (
          <span className={cls} style={{ ...style }}>
            {info}
          </span>
        )
      ) : (
        <div className={moreCls} style={{ ...style }}>
          <div className={`${Variable}-text__all`} ref={allRef}>
            {info}
            <span className={`${Variable}-text--link`} onClick={showClick}>
              {' '}
              收起
            </span>
          </div>
          <div className={`${Variable}-text__prepare`} ref={prepareRef}>
            {info}
          </div>
          {allHeight > prepareHeight && (
            <div className={`${Variable}-text__line`}>
              <div className={`${Variable}-text__line-all`}>{info}</div>
              {/* <div className={`${Variable}-text__line-overflow`}>{info}</div> */}
              <div className={`${Variable}-text__line-more`}>
                <span>...</span>
                <span className={`${Variable}-text--link`} onClick={showClick}>
                  更多
                </span>
              </div>
            </div>
          )}
        </div>
      )}
    </Fragment>
  );
}
