import React, { Fragment } from 'react';
import {
  Button,
  Card,
  Content,
  DatePicker,
  Form,
  Input,
  WithContext,
} from '../../../components/Fule';
import { createHashHistory } from 'history';

const history = createHashHistory();

function Edit(props) {
  const { actions } = props;
  let title = '';
  let pay = '';
  let time = '';
  //
  const onChange = (age, month, day, status) => {
    time = `${age}-${month}-${day}`;
    // console.log(1, time, status);
    return time;
  };
  // 确认提交
  const submitForm = (isEdit) => {
    const arr = {
      title,
      pay,
      time,
      cid: '1',
    };
    const issuccess = title.length && pay.length && time.length;
    if (!isEdit) {
      if (issuccess > 0) {
        actions.createItem(arr);
        history.push('/Pages/Bookkeeping');
      }
    } else {
    }
  };
  // 取消提交
  const cancelSubmit = () => {
    history.push('/Pages/Bookkeeping');
  };
  const onInput = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    if (name === 'itemName') {
      title = value;
    }
    if (name === 'itemNum') {
      pay = value;
    }
  };
  return (
    <Fragment>
      <Content>
        <Content.Header>创建信息</Content.Header>
        <Content.Body>
          <Card>
            <Card.Body>
              <Form allAlign="top">
                <Form.Item label="消费事件">
                  <Input
                    placeholder="请输入内容"
                    onChange={onInput}
                    name="itemName"
                  />
                </Form.Item>
                <Form.Item label="消费余额">
                  <Input
                    placeholder="请输入内容"
                    onChange={onInput}
                    name="itemNum"
                  />
                </Form.Item>
                <Form.Item label="时间" errorDes="请填写正确的格式">
                  <DatePicker
                    defaultValue="2020-20-20"
                    starAge="2020"
                    endAge="2028"
                    onClick={onChange}
                  ></DatePicker>
                </Form.Item>
              </Form>
            </Card.Body>
            <Card.Foot>
              <Button type="button" primary onClick={submitForm}>
                确定
              </Button>
              <Button type="submit" onClick={cancelSubmit}>
                取消
              </Button>
            </Card.Foot>
          </Card>
        </Content.Body>
      </Content>
    </Fragment>
  );
}
export default WithContext(Edit);
