import React from 'react';
import classNames from 'classnames';
import { Variable } from '../Utility/Utility';
import './index.scss';
export default function Input(props) {
  const {
    placeholder = '',
    onChange,
    onClick,
    value,
    size,
    status,
    name,
    className, // 自定义类名
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;
  const cls = classNames(
    className,
    `${Variable}-input`,
    size === 'full' ? `${Variable}-input--full` : '',
    status === 'error' ? 'is-error' : '',
  );
  return (
    <input
      placeholder={placeholder}
      name={name}
      className={cls}
      value={value}
      onChange={onChange}
      style={{ ...style }}
      {...rest}
      onClick={onClick}
    />
  );
}
