import React, { Fragment } from 'react';
import classNames from 'classnames';
import { Variable } from '../Utility/Utility';
import './index.scss';

export default function Media(props) {
  const {
    data = false,
    card = false,
    border = false,
    shadow = false,
    motion = false,
    className,
    style,
    ...rest
  } = props;
  const cls = classNames(className, `${Variable}-media`, {
    card: card || border || shadow || motion,
    border,
    shadow,
    motion,
  });
  return (
    <Fragment>
      <div className={cls} style={{ ...style }} {...rest}>
        <div className={`${Variable}-media__img`}>
          <img src={data.img} alt="" />
        </div>
        <div className={`${Variable}-media__info`}>
          <div className={`${Variable}-media__info-title`}>{data.title}</div>
          <div className={`${Variable}-media__info-des`}>{data.des}</div>
        </div>
      </div>
    </Fragment>
  );
}
