import React, { Fragment, useState } from 'react';
import classNames from 'classnames';
import { Variable, PublicContent } from '../Utility/Utility';
import './index.scss';

export default function Form(props) {
  const { className, allAlign, style, ...rest } = props;
  const cls = classNames(
    className,
    `${Variable}-form`,
    allAlign === 'middle' ? `${Variable}-form--middle` : '',
    allAlign === 'top' ? `${Variable}-form--top` : '',
    allAlign === 'bottom' ? `${Variable}-form--bottom` : ''
  );
  return (
    <Fragment>
      <div className={cls} style={style} {...rest}>
        {props.children}
      </div>
    </Fragment>
  );
}
function Item(props) {
  const {
    className, // 自定义类名
    label, // 行标题
    itemAlign, // 居中:middle   顶部:top  底部:bottom
    des, // 描述
    errorDes, // 错误描述
    status = 'normal', // 错误状态:error  成功状态:success 普通状态:normal
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;

  // item class
  const cls = classNames(
    className,
    `${Variable}-form__item`,
    itemAlign === 'middle' ? `${Variable}-form__item-middle` : '',
    itemAlign === 'top' ? `${Variable}-form--top` : '',
    itemAlign === 'bottom' ? `${Variable}-form--bottom` : ''
  );
  // 状态
  const [onStatus, setOnStatus] = useState(status);
  const stf = (data) => {
    setOnStatus(data);
  };
  // info class
  const infoCls = classNames(
    `${Variable}-form__item-info`,
    onStatus === 'error' ? 'is-error' : '',
    onStatus === 'success' ? 'is-success' : ''
  );
  return (
    <PublicContent.Provider value={stf}>
      <div className={cls} style={style} {...rest}>
        <div className={`${Variable}-form__item-label`}>
          <label>{label}</label>
        </div>
        <div className={infoCls}>
          {props.children}
          {(!!des || !!errorDes) && (
            <div className={`${Variable}-form__item-info--text`}>
              {!!errorDes && String(onStatus) === 'error' ? errorDes : des}
            </div>
          )}
        </div>
      </div>
    </PublicContent.Provider>
  );
}
Form.Item = Item;
