import React from 'react';
import { HashRouter as Router, Route, Switch } from 'react-router-dom';
import { Content } from '../../components/Fule';
import { subMenu } from './subMenu';
import C1 from './page/C1';
import C2 from './page/C2';
import './index.scss';
function MediaMenu() {
  return (
    <Router>
      <Content>
        <Content.Header>二级菜单</Content.Header>
        <Content.Body subMenu={subMenu}>
          <Switch>
            <Route exact path="/FuleSubMenu" component={C1} />
            <Route path="/FuleSubMenu/C1" component={C1} />
            <Route path="/FuleSubMenu/C2" component={C2} />
          </Switch>
        </Content.Body>
      </Content>
    </Router>
  );
}
export default MediaMenu;
