import React, { useState } from 'react';
import classNames from 'classnames';
import { TabPane } from './TanPane';
import { Variable, PublicContent } from '../Utility/Utility';
import './index.scss';

export default function Tab(props) {
  const { className, onTabClick, ceiling, ...rest } = props;

  const [actived, setActived] = useState(props.actived || 0);

  const cls = classNames(`${Variable}-tab`, className, {
    [`${Variable}-tab--ceiling`]: ceiling === true,
  });

  const tabClick = (index) => {
    setActived(index);
    onTabClick && onTabClick(index);
  };
  return (
    <PublicContent.Provider value={{ actived }}>
      <div className={cls} {...rest}>
        <div className={`${Variable}-tab__list`}>
          {props.children.length === undefined ? (
            <div className={`${Variable}-tab__list-item is-actived`}>
              <span className={`${Variable}-tab__list-item--text`}>
                {props.children.props.label}
              </span>
            </div>
          ) : (
            props.children.map((pane, index) => {
              const tabCls = classNames(`${Variable}-tab__list-item`, {
                'is-actived': index === actived,
              });
              return (
                <div
                  className={tabCls}
                  key={index}
                  onClick={() => tabClick(index)}
                >
                  <span className={`${Variable}-tab__list-item--text`}>
                    {pane.props.label}
                  </span>
                </div>
              );
            })
          )}
        </div>
        <div className={`${Variable}-tab__main`}>{props.children}</div>
      </div>
    </PublicContent.Provider>
  );
}

Tab.TabPane = TabPane;
