import React, { Fragment, useContext } from 'react';
import classNames from 'classnames';
import { Variable, PublicContent } from '../Utility/Utility';

export default function Item(props) {
  const {
    select,
    children, // 子代
    className, // 自定义类名
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;
  // let activedValue = null; //全局变量

  const stf = useContext(PublicContent);
  const Stf = (status) => {
    !!stf && !!stf.length && stf(status);
  };
  // const [activedValue, setActivedValue] = useState(null);
  const onClick = () => {
    // setActivedValue(props.index);
    Stf(children);
  };
  const cls = classNames(className, `${Variable}-select__item`, {
    'is-actived': props.value,
  });
  return (
    <Fragment>
      <div
        className={cls}
        style={{ ...style }}
        {...rest}
        onClick={(e) => onClick(e)}
      >
        {children}
      </div>
    </Fragment>
  );
}
