import React, { Fragment, useState, useEffect } from 'react';
import classNames from 'classnames';
import Icon from '../Icon';
import Overlay from '../Overlay';
import Item from './item';
import { Variable, PublicContent } from '../Utility/Utility';
import './index.scss';

// let defaultValue = null;

export default function Select(props) {
  const {
    text = '请选择',
    size = 'm', // s, m , l ,xl, full
    open = false,
    defaultValue,
    children, // 子代
    className, // 自定义类名
    onClick = () => {},
    popupClassName, // 弹窗的类名
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;

  // 子级点击获取数据
  const [value, setValue] = useState(!!defaultValue ? defaultValue : text);
  // const [defaultValue, setDefaultValue] = useState(null);
  const [colorText, setColorText] = useState(false);
  const stf = (data) => {
    setValue(data);
    setColorText(true);
  };
  // 默认class
  const cls = classNames(className, `${Variable}-select`, `size-${size}`);

  // 获取子组件点击时的值，并循环对比当前的选中状态，向子组件传递该状态
  const childrenWithProps = React.Children.map(children, (child, index) => {
    const { props } = child;
    return React.cloneElement(child, {
      value: value === props.children,
      defaultValue: !!props.select && props.children,
    });
  });
  // 传出值 , 从不同组件的函数体内部更新组件 ，放到 useEffect
  useEffect(() => {
    onClick(value);
  });
  return (
    <Fragment>
      <PublicContent.Provider value={stf}>
        <Overlay
          show={open}
          render={
            <div className={`${Variable}-select__list`}>
              {childrenWithProps}
            </div>
          }
          className={popupClassName}
          cuClassName={cls}
          cuStyle={{ ...style }}
          {...rest}
        >
          <div className={`${Variable}-select__inner`} onClick={onClick}>
            <div className={`${Variable}-select__content`}>
              <div
                className={classNames(`${Variable}-select__content-text`, {
                  'color-weak': !colorText && !defaultValue,
                })}
              >
                {value}
              </div>
              <div className={`${Variable}-select__content-icon`}>
                <Icon type="drop" />
              </div>
            </div>
          </div>
        </Overlay>
      </PublicContent.Provider>
    </Fragment>
  );
}
Select.Item = Item;
