import React, { useContext } from 'react';
import classNames from 'classnames';
import { Variable, PublicContent } from '../Utility/Utility';
import './index.scss';

export function TabPane(props) {
  const { className, children, index } = props;

  const tabs = useContext(PublicContent);

  const cls = classNames(`${Variable}-tab__panel`, className, {
    'is-actived': Number(index) === tabs.actived,
  });
  return <div className={cls}>{children}</div>;
}
