import React, { Fragment } from 'react';
import classNames from 'classnames';
import { Variable } from '../Utility/Utility';
import './index.scss';
export default function Icon(props) {
  const { className, left, right, style, ...rest } = props;
  const cls = classNames(className, `${Variable}-justify`);
  return (
    <Fragment>
      <div className={cls} style={{ ...style }} {...rest}>
        {left && <div className={`${Variable}-justify__col`}>{left}</div>}
        {right && <div className={`${Variable}-justify__col`}>{right}</div>}
      </div>
    </Fragment>
  );
}
