import React, { Fragment, useContext } from 'react';
import classNames from 'classnames';
import { Variable, PublicContent } from '../Utility/Utility';

export default function Item(props) {
  const {
    select,
    children, // 子代
    className, // 自定义类名
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;
  // let activedValue = null; //全局变量

  const stf = useContext(PublicContent);
  const Stf = (status) => {
    !!stf && !!stf.length && stf(status);
  };
  // console.log(props.index)
  const onClick = () => {
    Stf(children);
  };
  const cls = classNames(className, `${Variable}-radio__item`, {
    'is-actived': props.value,
  });

  return (
    <Fragment>
      <label
        className={cls}
        style={{ ...style }}
        {...rest}
        onClick={(e) => onClick(e)}
      >
        <span className={`${Variable}-radio__item-dot`}>
          <input
            readOnly
            type="radio"
            className={`${Variable}-input--radio`}
            checked={props.value}
          />
          <i className={`${Variable}-radio__item-dot--des`}></i>
        </span>
        <span className={`${Variable}-radio__item-text`}>{children}</span>
      </label>
    </Fragment>
  );
}
