import React, { Fragment, useRef, useState, useEffect } from 'react';
import classNames from 'classnames';
import Item from './item';
import { Variable, PublicContent } from '../Utility/Utility';
import './index.scss';

// let count = 0;
export default function MediaBanner(props) {
  const {
    children, // 子代
    interval = 4000, // 定时器时间
    animationTime = 400, // 切换时动画时长
    autoPlay = true, // 开启自动播放
    dotDom = true, // 是否开启dot节点
    dotAction = true, // 是否开启切换按钮节点
    onChange = () => {}, // 函数
  } = props;
  const dataLength = children.length; // 子代数组长度
  let timer = null; // 定时器
  // 要滑动的宽度
  const anchoRef = useRef(null);
  const [domWidth, setDomWidth] = useState(0);
  const wi = () => {
    const anchorRectWidth = anchoRef.current && anchoRef.current.clientWidth;
    setDomWidth(anchorRectWidth);
  };
  useEffect(() => {
    wi();
    window.onresize = () => {
      wi();
    };
    return () => {
      window.onresize = null;
    };
  });

  // 轮播参数
  const [current, setCurrent] = useState(0); // 下标
  const [distance, setDistance] = useState(0); // 滑动距离
  const [time, setTime] = useState(animationTime); // 动画时间
  const [autoPlayData, setAutoPlayData] = useState(autoPlay); // 是否开启自动切换
  // 点击滑动
  const tabActived = (index) => {
    setCurrent(index);
    onChange && onChange(index);
    setTime(400);
    setDistance(-domWidth * index);
  };

  // 下一页
  const tabNext = () => {
    setTime(400);
    setDistance(-domWidth * (current + 1));
    setCurrent(current + 1);
    if (current >= dataLength - 1) {
      setCurrent(0);
      setTimeout(() => {
        setTime(0);
        setDistance(0);
      }, 400);
    }
  };

  // 上一页
  const tabPrev = () => {
    setCurrent(current - 1);
    if (current === 0) {
      setCurrent(dataLength - 1);
      setTime(0);
      setDistance(-domWidth * dataLength);
      setTimeout(() => {
        setTime(400);
        setDistance(-domWidth * (dataLength - 1));
      }, 0);
    } else {
      setTime(400);
      setDistance(-domWidth * (current - 1));
    }
  };

  // 定时器
  useEffect(() => {
    if (autoPlayData) {
      // eslint-disable-next-line react-hooks/exhaustive-deps
      timer = setInterval(tabNext, interval);
    }
    return () => {
      clearInterval(timer);
    };
  }, [timer, autoPlayData]);
  const _mouseOver = () => {
    !!autoPlay && setAutoPlayData(false);
  };
  const _mouseLeave = () => {
    !!autoPlay && setAutoPlayData(true);
  };
  const sty = {
    transform: `translateX(${distance}px)`,
    transition: `transform ${time}ms ease-in-out`,
  };
  return (
    <Fragment>
      <PublicContent.Provider value={{ dataLength, current, domWidth }}>
        <div
          className={`${Variable}-mediaBanner`}
          onMouseOver={_mouseOver}
          onMouseLeave={_mouseLeave}
        >
          <div className={`${Variable}-mediaBanner__content`} ref={anchoRef}>
            <div className={`${Variable}-mediaBanner__content-inner`}>
              <div className={`${Variable}-mediaBanner__list`} style={sty}>
                {children}
                {children}
              </div>
            </div>
            {dataLength > 1 && !!dotAction && (
              <div className={`${Variable}-mediaBanner__content-action`}>
                <div
                  className={`${Variable}-mediaBanner__content-action--prev`}
                  onClick={tabPrev}
                >
                  &#10143;
                </div>
                <div
                  className={`${Variable}-mediaBanner__content-action--next`}
                  onClick={tabNext}
                >
                  &#10143;
                </div>
              </div>
            )}
          </div>
          {dataLength > 1 && !!dotDom && (
            <div className={`${Variable}-mediaBanner__dot`}>
              {children.map((item, index) => {
                const tabCls = classNames(`${Variable}-mediaBanner__dot-item`, {
                  'is-actived': index === current,
                });
                return (
                  <span
                    className={tabCls}
                    key={index}
                    onClick={() => tabActived(index)}
                  ></span>
                );
              })}
            </div>
          )}
        </div>
      </PublicContent.Provider>
    </Fragment>
  );
}
MediaBanner.Item = Item;
