import React, { Fragment, useState, useEffect } from 'react';
import classNames from 'classnames';
import Item from './item';
import { Variable, PublicContent } from '../Utility/Utility';
import './index.scss';

// let defaultValue = null;

export default function Radio(props) {
  const {
    text = '请选择',
    type = 'basic', // basic, btn
    size = 'm', // s, m , l ,xl, full
    defaultValue,
    children, // 子代
    className, // 自定义类名
    onClick = () => {},
  } = props;

  // 子级点击获取数据
  const [value, setValue] = useState(!!defaultValue ? defaultValue : text);

  const stf = (data) => {
    setValue(data);
  };
  // 默认class
  const cls = classNames(
    className,
    `${Variable}-radio`,
    `size-${size}`,
    `${Variable}-radio--${type}`
  );

  // 获取子组件点击时的值，并循环对比当前的选中状态，向子组件传递该状态, 一般选择的项内容不会相同
  const childrenWithProps = React.Children.map(children, (child, index) => {
    const { props } = child;
    return React.cloneElement(child, {
      // index: index,
      value: value === props.children,
      defaultValue: !!props.select && props.children,
    });
  });
  // 传出值 , 从不同组件的函数体内部更新组件 ，放到 useEffect
  useEffect(() => {
    onClick(value);
  });
  return (
    <Fragment>
      <PublicContent.Provider value={stf}>
        <div className={cls}>{childrenWithProps}</div>
      </PublicContent.Provider>
    </Fragment>
  );
}
Radio.Item = Item;
