import React, { useContext } from 'react';
import classNames from 'classnames';
import { Variable, PublicContent } from '../Utility/Utility';
import './index.scss';

export default function Item(props) {
  const { className, children, index } = props;

  const pData = useContext(PublicContent);
  const { current } = pData;

  const cls = classNames(`${Variable}-mediaBanner__item`, className, {
    'is-actived': Number(index) === current + 1,
  });
  return (
    <div className={cls}>
      <div className={`${Variable}-mediaBanner__item-inner`}>{children}</div>
    </div>
  );
}
