import React from 'react';
import classNames from 'classnames';
import { Variable } from '../Utility/Utility';
import './index.scss';
export default function Avatar(props) {
  const {
    data,
    className,
    children,
    ...rest // 自定义其他属性
  } = props;
  const cls = classNames(className, `${Variable}-avatar`);
  return (
    <div className={cls} {...rest}>
      <ul className={`${Variable}-avatar__list`}>
        {data.map((item, index) => (
          <li
            key={`li-${index}`}
            style={{ backgroundImage: `url(${item})` }}
            className={`${Variable}-avatar__list-item`}
          ></li>
        ))}
      </ul>
    </div>
  );
}
