import React from 'react';
import classNames from 'classnames';
import { Variable } from '../Utility/Utility';
import { IconsData } from './IconsData';
import './index.scss';
export default function Icon(props) {
  const { className, size = 16, type, style, hover, onClick, ...rest } = props;
  const cls = classNames(
    className,
    `${Variable}-icon ${Variable}-icon--${type} icon-${size}`,
    hover ? 'is-hover' : ''
  );
  return (
    <i
      className={cls}
      style={style}
      {...rest}
      onClick={onClick}
      dangerouslySetInnerHTML={{ __html: IconsData[type] }}
    ></i>
  );
}
