import React, { Fragment, useState, useEffect } from 'react';
import ReactDOM from 'react-dom';
import classNames from 'classnames';
import { Variable } from '../Utility/Utility';
import { Manager, Reference, Popper } from 'react-popper';
import './index.scss';
export default function Overlay(props) {
  const {
    className = '', // 自定义类名
    placement,
    trigger,
    render,
    close,
    cuClassName,
    cuStyle,
    show = false,
    ...rest // 自定义其他属性
  } = props;
  // 设置空的定时器变量
  let timer = null;
  // 点击 关闭icon
  const [isOpen, setIsOpen] = useState(show);
  const switchIsOpen = (e) => {
    setIsOpen(!isOpen);
    // 取消冒泡
    // e.stopPropagation();
    // e.stopImmediatePropagation();
    // e.nativeEvent.stopImmediatePropagation();
    // 默认事件
    e.preventDefault();
  };
  // 点击body 隐藏

  const hide = () => {
    // 使用定时器避免点击的时候多次调用
    timer = setTimeout(() => {
      setIsOpen(false);
    }, 10);
  };
  useEffect(() => {
    if (isOpen) {
      document.querySelector('#root').addEventListener('click', hide);
    }
    return () => {
      document.querySelector('#root').removeEventListener('click', hide);
      clearTimeout(timer);
    };
  });
  const cls = classNames(className, `${Variable}-overlay`);
  return (
    <Fragment>
      <Manager>
        <Reference>
          {({ ref }) => (
            <div
              ref={ref}
              style={{ ...cuStyle }}
              className={cuClassName}
              {...rest}
              onClick={switchIsOpen}
            >
              {props.children}
            </div>
          )}
        </Reference>
        <Popper placement="bottom-start">
          {({ ref, style, placement, arrowProps }) =>
            isOpen && (
              <Fragment>
                {ReactDOM.createPortal(
                  <Fragment>
                    <div
                      className={cls}
                      ref={ref}
                      style={style}
                      data-placement={placement}
                    >
                      <div className={`${Variable}-overlay__inner`}>
                        {render}
                      </div>
                    </div>
                  </Fragment>,
                  document.querySelector('#popup')
                )}
              </Fragment>
            )
          }
        </Popper>
      </Manager>
    </Fragment>
  );
}
