import React, { Fragment } from 'react';
import classNames from 'classnames';
import { Variable } from '../Utility/Utility';
// import * as addon from './addons';
import './index.scss';
export default function Table(props) {
  const { className, verticalTop, records, columns, merge } = props;
  return (
    <Fragment>
      <div
        className={classNames(
          `${Variable}-table`,
          className,
          verticalTop ? `${Variable}-table--verticaltop` : ''
        )}
      >
        <div className={`${Variable}-table__header`}>
          <table className={classNames(`${Variable}-table__box`)}>
            <ColGrou columns={columns} />
            <Header records={records} columns={columns} />
          </table>
        </div>
        <div className={`${Variable}-table__body`}>
          <table className={classNames(`${Variable}-table__box`)}>
            <ColGrou columns={columns} />
            <Body records={records} columns={columns} merge={merge} />
          </table>
        </div>
      </div>
    </Fragment>
  );
}
function Header(props) {
  const { columns } = props;
  return (
    <Fragment>
      <thead>
        <tr>
          {!!columns &&
            columns.map((item, index) => (
              <th key={index}>
                {typeof item.header === 'function' ? (
                  item.header()
                ) : (
                  <div>
                    <span className={`${Variable}-text--overflow`}>
                      {item.header}
                    </span>
                  </div>
                )}
              </th>
            ))}
        </tr>
      </thead>
    </Fragment>
  );
}
function Body(props) {
  const { columns } = props;
  let { records } = props;
  // let arr;
  // let currentProfileIndex = [];
  // if (merge) {
  //   const s = new Set();
  //   records.forEach((i) => {
  //     s.add(i.name);
  //   });
  //   arr = Array.from(s.values());
  // }
  // merge &&
  //   arr.forEach((i) => {
  //     currentProfileIndex.push(
  //       (records || []).findIndex((records) => records.name === i)
  //     );
  //   });
  // console.log(currentProfileIndex);
  const newArray = records.reduce((total, data) => {
    let list = total.get(data.name);
    if (!list) {
      list = [];
      total.set(data.name, list);
    }
    list.push(data);
    return total;
  }, new Map());
  // records = Array.from(_map.entries());
  records = [...newArray];
  // td
  const funTd = (dataTr, rowLength, column, indexTr, indexTd) => {
    if (indexTr === 0 && indexTd === 0 && rowLength > 1 && records.length > 1) {
      return (
        <td key={indexTd} rowSpan={rowLength} className="rowspan">
          {!!column.render ? (
            column.render(dataTr)
          ) : (
            <div>
              {column.key === 'cost' ? (
                <span className={`${Variable}-text--overflow`}>
                  {dataTr[column.key]}
                </span>
              ) : (
                <span className={`${Variable}-text--overflow`}>
                  {dataTr[column.key]}
                </span>
              )}
            </div>
          )}
        </td>
      );
    }
    if (indexTr > 0 && indexTd === 0 && rowLength > 1 && records.length > 1) {
      return;
    }
    return (
      <td key={indexTd}>
        {!!column.render ? (
          column.render(dataTr)
        ) : (
          <div>
            {column.key === 'cost' ? (
              <span className={`${Variable}-text--overflow`}>
                {dataTr[column.key]}
              </span>
            ) : (
              <span className={`${Variable}-text--overflow`}>
                {dataTr[column.key]}
              </span>
            )}
          </div>
        )}
      </td>
    );
  };
  return (
    <Fragment>
      <tbody>
        {!records.length && (
          <tr>
            <td
              colSpan={columns.length}
              className={`${Variable}-text ${Variable}-text--center`}
            >
              <div>没有数据</div>
            </td>
          </tr>
        )}
        {!!records &&
          records.map((record) =>
            record[1].map((dataTr, indexTr) => (
              <tr key={indexTr}>
                {columns &&
                  columns.map((column, indexTd) => {
                    const rowLength = record[1].length;
                    return funTd(dataTr, rowLength, column, indexTr, indexTd);
                  })}
              </tr>
            ))
          )}
      </tbody>
    </Fragment>
  );
}
function ColGrou(props) {
  const { columns } = props;

  return (
    <colgroup>
      {!!columns &&
        columns.map((item, index) => <col key={item.key} width={item.width} />)}
    </colgroup>
  );
}
