import React, { Fragment, useContext } from 'react';
import classNames from 'classnames';
import { Variable, PublicContent } from '../Utility/Utility';

export default function Item(props) {
  const {
    select,
    children, // 子代
    className, // 自定义类名
    style, // 自定义样式
    ...rest // 自定义其他属性
  } = props;
  const stf = useContext(PublicContent);
  const Stf = (status) => {
    !!stf && !!stf.length && stf(status);
  };
  // console.log(props.index)
  const onClick = () => {
    Stf(children);
  };
  const cls = classNames(className, `${Variable}-checkout__item`, {
    'is-actived': props.value,
  });

  return (
    <Fragment>
      <label className={cls} style={{ ...style }} {...rest}>
        <input
          type="checkbox"
          className={`${Variable}-checkout__item-input`}
          checked={props.value}
          onChange={(e) => onClick(e)}
        />
        <span className={`${Variable}-checkout__item-text`}>{children}</span>
      </label>
    </Fragment>
  );
}
