import React, { Fragment } from 'react';
import ReactTooltip from 'react-tooltip';
import { Variable } from '../Utility/Utility';
import './index.scss';
export default function Tooltip(props) {
  const { content } = props;
  return (
    <Fragment>
      <ReactTooltip />
      <div className={`${Variable}-toolptip`} data-tip={content}>
        {props.children}
      </div>
    </Fragment>
  );
}
