import {
  Variable,
  Vdata,
  VpadLeft,
  ID,
  ChineseNumeral,
} from '../../components/Utility/Utility';
export const CssVar = `
~~~css
// 面板色使用: @include 函数名('去$的变量名')
// @include themeBgcolor('themeBgcolor');
// @include themeBorderColor('themeBorderColor');
// @include themeBlock('themeBlock');
// @include themeContent('themeContent');
@mixin themeBgcolor($color) {
  @include themeify {
    background-color: themed($color);
  }
}
@mixin themeBorderColor($color) {
  @include themeify {
    border-color: themed($color);
  }
}
@mixin themeBlock($color) {
  @include themeify {
    background-color: themed($color);
  }
}
@mixin themeContent($color) {
  @include themeify {
    background-color: themed($color);
  }
}

//border 使用：@include border-color(border-color);
@mixin border-color($color) {
  @include themeify {
    border-color: themed($color);
  }
}
//text 使用：@include text-color(text-color);
@mixin text-color($color) {
  @include themeify {
    color: themed($color);
  }
}
// 表单 使用：@include form-text-color(form-text-color);
@mixin form-text-color($color) {
  @include themeify {
    color: themed($color);
  }
}
// btn 字体 使用：@include btn-text-color(btn-text-color);
@mixin btn-text-color($color) {
  @include themeify {
    color: themed($color);
  }
}
// btn 边框 使用：@include btn-border-color(btn-border-color);
@mixin btn-border-color($color) {
  @include themeify {
    border-color: themed($color);
  }
}
// btn 背景 使用：@include btn-bgcolor(btn-bgcolor);
@mixin btn-bgcolor($color) {
  @include themeify {
    background-color: themed($color);
  }
}

// select 背景 使用：@include select-bgcolor(select-bgcolor);
@mixin select-bgcolor($color) {
  @include themeify {
    background-color: themed($color);
  }
}
~~~`;

