import React, { Fragment } from 'react';
// BrowserRouter 可跟服务器配合 , HashRouter 不跟服务器配合, MemoryRouter 不修改url
// import {BrowserRouter as Router, Route, NavLink} from 'react-router-dom';
import { HashRouter as Router, Route, Switch } from 'react-router-dom';
// import {MemoryRouter as Router, Route, NavLink} from 'react-router-dom';

import './index.scss';
// 组件
import { Container, SideMenu, Switch as Aswitch } from '../../components/Fule';
// 模块
import FuleContent from '../../modular/FuleContent';
import FuleSubMenu from '../../modular/FuleSubMenu';
import FuleText from '../../modular/FuleText';
import FulePopover from '../../modular/FulePopover';
import FuleGrid from '../../modular/FuleGrid';
import FuleCard from '../../modular/FuleCard';
import FuleButton from '../../modular/FuleButton';
import FuleTable from '../../modular/FuleTable';
import FuleDatePicker from '../../modular/FuleDatePicker';
import FuleTab from '../../modular/FuleTab';
import FuleIcon from '../../modular/FuleIcon';
import FuleInput from '../../modular/FuleInput';
import FuleForm from '../../modular/FuleForm';
import FuleAnchor from '../../modular/FuleAnchor';
import FuleMedia from '../../modular/FuleMedia';
import FuleBanner from '../../modular/FuleBanner';
import FuleUtility from '../../modular/FuleUtility';
import FuleAnimation from '../../modular/FuleAnimation';
import FuleSelect from '../../modular/FuleSelect';
import FuleRadio from '../../modular/FuleRadio';
import FuleCheckbox from '../../modular/FuleCheckbox';
import FullTool from '../../modular/FullTool';
import PluginsD3 from '../../modular/PluginsD3';
import PluginsFlow from '../../modular/PluginsFlow';
import PluginsThree from '../../modular/PluginsThree';
import PluginsCanvas from '../../modular/PluginsCanvas';
import Pages from '../../modular/Pages';
import FuleCss from '../../modular/FuleCss';
import FuleScrollMore from '../../modular/FuleScrollMore';
import FuleSwitch from '../../modular/FuleSwitch';
// 菜单数据
import muenList from './muenList';
import logo from './img/logo-black.svg';
export default function Home() {
  const getAswitch = (data) => {
    const a = document.querySelector('#root'); //dom
    if (!!data) {
      a.setAttribute('data-theme', 'dark');
    } else {
      a.setAttribute('data-theme', 'light');
    }
  };
  return (
    <Fragment>
      <Container full theme="white">
        <Container.Header
          logo={logo}
          opt={<Aswitch label="主题变色" onChange={getAswitch} />}
        ></Container.Header>
        <Container.Body>
          <SideMenu title="React 组件" data={muenList}></SideMenu>
          <Router>
            <Switch>
              <Route exact path="/" component={FuleSubMenu} />
              <Route path="/FuleSubMenu" component={FuleSubMenu} />
              <Route path="/FuleContent" component={FuleContent} />
              <Route path="/FuleText" component={FuleText} />
              <Route path="/FuleIcon" component={FuleIcon} />
              <Route path="/FuleButton" component={FuleButton} />
              <Route path="/FuleInput" component={FuleInput} />
              <Route path="/FulePopover" component={FulePopover} />
              <Route path="/FuleRadio" component={FuleRadio} />
              <Route path="/FuleCheckbox" component={FuleCheckbox} />
              <Route path="/FuleDatePicker" component={FuleDatePicker} />
              <Route path="/FuleForm" component={FuleForm} />
              <Route path="/FuleSwitch" component={FuleSwitch} />
              <Route path="/FuleGrid" component={FuleGrid} />
              <Route path="/FuleCard" component={FuleCard} />
              <Route path="/FuleTable" component={FuleTable} />
              <Route path="/FuleTab" component={FuleTab} />
              <Route path="/FuleAnchor" component={FuleAnchor} />
              <Route path="/FuleMedia" component={FuleMedia} />
              <Route path="/FuleBanner" component={FuleBanner} />
              <Route path="/FuleScrollMore" component={FuleScrollMore} />
              <Route path="/FuleUtility" component={FuleUtility} />
              <Route path="/FuleAnimation" component={FuleAnimation} />
              <Route path="/FuleSelect" component={FuleSelect} />
              <Route path="/FullTool" component={FullTool} />
              <Route path="/PluginsD3" component={PluginsD3} />
              <Route path="/PluginsFlow" component={PluginsFlow} />
              <Route path="/PluginsThree" component={PluginsThree} />
              <Route path="/PluginsCanvas" component={PluginsCanvas} />
              <Route path="/FuleCss" component={FuleCss} />
              <Route path="/Pages" component={Pages} />
            </Switch>
          </Router>
        </Container.Body>
      </Container>
    </Fragment>
  );
}
