import React, { useState, useEffect } from 'react';
import './index.scss';
import { Icon, Content, Media } from '../../components/Fule';
import { mediaData } from '../../mock/data';

export default function FuleScrollMore() {
  const scrollSpac = 176; // 滚动的距离参数
  const [scrollResult, setScrollResult] = useState(0); // 滚动停止最终位置
  const [showNext, setShowNext] = useState(true); // 控制显示next
  // 左滑
  const funPrev = () => {
    let num = scrollResult - scrollSpac;
    if (num < 0) {
      num = 0;
    }
    setShowNext(true);
    setScrollResult(num);
  };
  // 右滑
  const funNext = () => {
    const itemDom = document.querySelector('.FuleScrollMore-list'); //dom
    const itemDomWidth = itemDom.getBoundingClientRect().width || null; //dom宽度
    const itemDomScrollWidth = itemDom.scrollWidth || null; // 滚动宽度
    let num = scrollResult + scrollSpac;
    if (num >= itemDomScrollWidth - itemDomWidth) {
      num = itemDomScrollWidth - itemDomWidth;
      setShowNext(false);
    }
    setScrollResult(num);
  };
  const scrollItem = (result) => {
    const itemDom = document.querySelector('.FuleScrollMore-list'); //dom
    const scrollLeftValue = itemDom.scrollLeft; // 元素滚动条到元素左边的距离
    if (result > scrollLeftValue) {
      setTimeout(() => {
        itemDom.scrollLeft = scrollLeftValue + 2;
        scrollItem(result);
      }, 1);
    }
    if (result < scrollLeftValue) {
      setTimeout(() => {
        itemDom.scrollLeft = scrollLeftValue - 2;
        scrollItem(result);
      }, 1);
    }
  };
  useEffect(() => {
    scrollItem(scrollResult);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [scrollResult]);
  // // 一直监听原生滚动
  useEffect(() => {
    const itemDom = document.querySelector('.FuleScrollMore-list'); //dom
    const itemDomWidth =
      Math.floor(itemDom.getBoundingClientRect().width) || null; //dom宽度，会比 scrollWidth 获取的多一点，采用向下取整
    const itemDomScrollWidth = itemDom.scrollWidth || null; // 滚动宽度
    const funScroll = () => {
      itemDom.onscroll = () => {
        const scrollLeftValue = itemDom.scrollLeft; // 元素滚动条到元素左边的距离
        setScrollResult(scrollLeftValue);
        console.log(scrollLeftValue, itemDomScrollWidth - itemDomWidth);
        if (
          scrollLeftValue > itemDomScrollWidth - itemDomWidth - 1 ||
          scrollLeftValue === itemDomScrollWidth - itemDomWidth
        ) {
          setShowNext(false);
        } else {
          setShowNext(true);
        }
      };
      if (itemDomScrollWidth === itemDomWidth) {
        setShowNext(false);
      }
    };
    funScroll();
    window.onresize = () => {
      funScroll();
    };
  });
  return (
    <Content>
      <Content.Header>单选</Content.Header>
      <Content.Body>
        <div className="FuleScrollMore">
          <div className="FuleScrollMore-inner">
            <div className="FuleScrollMore-list">
              {mediaData.map((item, index) => (
                <div
                  className="FuleScrollMore-item"
                  key={`FuleScrollMore-item-${index}`}
                >
                  <Media data={item} />
                </div>
              ))}
            </div>
            <div className="FuleScrollMore-action">
              {scrollResult !== 0 && (
                <div
                  className="FuleScrollMore-action__prev"
                  role="button"
                  onClick={funPrev}
                >
                  <Icon type="arrow" hover size="16" />
                </div>
              )}
              {!!showNext && (
                <div
                  className="FuleScrollMore-action__next"
                  role="button"
                  onClick={funNext}
                >
                  <Icon type="arrow" hover size="16" />
                </div>
              )}
            </div>
          </div>
        </div>
      </Content.Body>
    </Content>
  );
}
