import React, { useState } from 'react';
import cn from 'classnames';
import { Variable } from '../Utility/Utility';
import './index.scss';
export default function Switch(props) {
  const {
    className, // 自定义类名
    style, // 自定义样式
    type, // 按钮，a 标签
    label = '',
    value = false,
    loading,
    disabled,
    onChange = () => {},
    ...rest // 自定义其他属性
  } = props;
  const cls = cn(className, `${Variable}-switch`);
  const [checkedValue, setCheckedValue] = useState(value);
  const funChange = () => {
    onChange(!checkedValue);
    setCheckedValue(!checkedValue);
  };
  return (
    <label className={cls} style={style} {...rest}>
      <span className={`${Variable}-switch__btn`}>
        <input
          type="checkbox"
          className={`${Variable}-switch__btn-input`}
          checked={checkedValue}
          onChange={funChange}
          disabled={disabled}
        />
        <span className={`${Variable}-switch__btn-spot`}></span>
      </span>
      {!!label && <span className={`${Variable}-switch__text`}>{label}</span>}
    </label>
  );
}
